import "./consiglio.js";
import { Pagina } from "../../commons/js/pagina.js";

$(document).ready(function () {

    var urlParams = new URLSearchParams(window.location.search);
    const idTipo = urlParams.get('tipo');
    const idOggetto = urlParams.get('id');
    const idOrganismo = urlParams.get('organismo');

    renderContenuto();
    renderBreadcrumb();

    function renderContenuto() {
        var urlSezione = apibase.baseurl + 'oi/' + idTipo + '/' + idOggetto;
        return $.ajax({
            url: urlSezione,
            success: function (result) {
                $("#title").text(result.title);
                $("#container_base").append(Pagina.renderPaginaBase(result));
            }, error: function (res_err) {
                window.location = "notFound.html";
            }
        });
    }

    function renderBreadcrumb() {
        var urlListaOrganismi = apibase.baseurl + "/oi/10400"
        $.ajax({
            url: urlListaOrganismi, success: function (result) {
                if (result) {
                    for (let organismo of result.content) {
                        if (organismo.id == idOrganismo) {
                            const nomeSezione = idTipo == 10403 ? 'In Evidenza' : 'Notizie';
                            let breadcrumb = '<li class="breadcrumb-item"><a href="pagina-organismo.html?id=' + idOrganismo + '">' + organismo.title + '</a><span class="separator">/</span></li>';
                            breadcrumb += '<li class="breadcrumb-item"><a href="archivio-organismo.html?tipo=' + idTipo + '&organismo=' + idOrganismo + '">' + nomeSezione + '</a></li>';
                            $('#bread').append(breadcrumb)
                            return;
                        }
                    }
                }

            }
        });
    }

});